<template>
  <div class="cms-list-layout" v-loading="loading">
    <cms-breadcrumb to="/shopMember/list" type="list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <el-form
      ref="form"
      :rules="rules"
      :model="formData"
      label-width="210px"
      v-if="formData.memberExt"
    >
      <el-form-item label="用户名">
        <span>{{formData.username}}</span>
      </el-form-item>
      <el-form-item label="会员头像" prop="resourceDataId">
        <cms-picture-zone v-model="formData.resourceDataId" :data="formData.memberExt.resourceData"></cms-picture-zone>
      </el-form-item>
      <el-form-item label="账户余额">
        <span>{{formData.memberAccount.balance}}</span>
      </el-form-item>
      <el-form-item label="积分">
        <span>{{formData.memberAccount.score}}</span>
      </el-form-item>
      <el-form-item label="会员成长值/会员等级">
        <span>{{formData.memberAccount.exp}}</span> /
        <span>{{formData.memberLevel.levelName }}</span>
      </el-form-item>
      <el-form-item label="邮箱" prop="email" :error="errorMessage.email">
        <el-input maxlength="20" v-model="formData.email"></el-input>
      </el-form-item>
      <el-form-item label="手机" prop="phone">
        <el-input maxlength="11" v-model="formData.phone"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="formData.password" type="password"></el-input>
        <div class="gray">留空则不修改</div>
      </el-form-item>
      <el-form-item label="昵称" prop="nickname">
        <el-input maxlength="30" v-model="formData.nickname"></el-input>
      </el-form-item>
      <el-form-item label="性别">
        <el-radio-group v-model="formData.memberExt.gender">
          <el-radio :label="1">男</el-radio>
          <el-radio :label="2">女</el-radio>
          <el-radio :label="3">保密</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="生日">
        <el-date-picker
          :picker-options="pickerOptions"
          v-model="formData.memberExt.birthday"
          type="date"
          size="small"
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="是否启用">
        <cms-switch v-model="formData.enabled"></cms-switch>
        <div class="gray">设置为黑名单后，该会员将无法登录商城</div>
      </el-form-item>
    </el-form>
    <!-- <h3 class="cms-segmentation">账户绑定</h3>
    <el-form ref="form" :model="formData" label-width="210px">
      <el-form-item label="QQ">
        <span v-if="index3 ===0">未绑定</span>
        <span v-if="index3 === 3">
          已绑定
          <a href="javascript:;" class="unbind" @click.prevent="unbind('qq')">解绑</a>
        </span>
      </el-form-item>
      <el-form-item label="微博">
        <span v-if="index2 ===0">未绑定</span>
        <span v-if="index2 === 2">
          已绑定
          <a href="javascript:;" class="unbind" @click.prevent="unbind('sina')">解绑</a>
        </span>
      </el-form-item>
      <el-form-item label="微信">
        <span v-if="index ===0">未绑定</span>
        <span v-if="index ===1">
          已绑定
          <a href="javascript:;" class="unbind" @click.prevent="unbind('wechat')">解绑</a>
        </span>
      </el-form-item>
    </el-form> -->
    <div class="cms-form-footer">
      <cms-button @click="update" v-color>保存</cms-button>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  memberGet,
  memberUpdate,
  checkEmail,
  checkPhone,
  memberThirdDelete
} from '@/api/users/user.js'
export default {
  mixins: [form],
  data() {
    var isNickname = (rule, value, callback) => {
      const reg = /^([\u4E00-\u9FA5a-zA-Z0-9]+)$/
      if (value === '') {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error('昵称格式不正确'))
      } else {
        callback()
      }
    }
    var isEmail = (rule, value, callback) => {
      // 验证邮箱唯一
      checkEmail({ email: value, id: this.formData.id }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('邮箱已存在，不允许重复添加'))
          }
        }
      })
    }
    var isPhone = (rule, value, callback) => {
      // 验证电话唯一
      const reg = /^1[3456789]\d{9}$/
      if (value === '') {
        callback()
      } else {
        if (!reg.test(value)) {
          callback(new Error('手机号码格式不正确'))
        } else {
          checkPhone({ phone: value, id: this.formData.id }).then(res => {
            if (res.code === 200) {
              if (res.data) {
                callback()
              } else {
                callback(new Error('手机已存在，不允许重复添加'))
              }
            }
          })
        }
      }
    }
    var isPwd = (rule, value, callback) => {
      var reg = /^\d{6,16}$/
      if (value !== '' && !reg.test(value)) {
        callback(new Error('请输入6-16位数密码'))
      } else {
        callback()
      }
    }
    return {
      formData: {},
      index: 0, // 微信
      index2: 0, // 微博
      index3: 0, // qq
      rules: {
        email: [
          rules.required('此项为必填'),
          rules.email(),
          rules.custom(isEmail)
        ],
        password: [rules.custom(isPwd)],
        phone: [rules.phone(), rules.custom(isPhone)],
        nickname: [rules.custom(isNickname)]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      }
    }
  },
  methods: {
    getFormData(id) {
      this.loading = true
      // 通过id获取表单信息
      memberGet({ id: id }).then(res => {
        if (res.code === 200) {
          if (res.data.memberExt.birthday) {
            res.data.memberExt.birthday = this.$moment(
              res.data.memberExt.birthday
            ) // 格式化时间
          }
          for (let i = 0; i < res.data.memberThirds.length; i++) {
            if (res.data.memberThirds[i].thirdTypeCode === 'wechat') {
              // 判断是否绑定微信
              this.index = 1
            }
            if (res.data.memberThirds[i].thirdTypeCode === 'sina') {
              // 判断是否绑定微博
              this.index2 = 2
            }
            if (res.data.memberThirds[i].thirdTypeCode === 'qq') {
              // 判断是否绑定QQ
              this.index3 = 3
            }
          }
          this.formData = Object.assign(
            { resourceDataId: null, password: '' },
            res.data
          )
          this.loading = false
        } else {
          this.loading = false
          this.$errorMessage(res.message)
        }
      })
    },
    unbind(type) {
      // 解绑
      this.$confirm('确定解除绑定?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids = []
        this.formData.memberThirds.forEach(v => {
          if (v.thirdTypeCode === type) {
            ids.push(parseInt(v.id))
          }
        })
        memberThirdDelete({ ids: ids }).then(res => {
          if (res.code === 200) {
            this.$successMessage('解绑成功')
            setTimeout(() => {
              this.getFormData(this.$route.query.id)
            }, 500)
          } else {
            this.errorMessage(res.message)
          }
        })
      })
    },
    update() {
      // 提交表单
      const params = {
        member: {
          email: this.formData.email,
          enabled: this.formData.enabled,
          id: this.formData.id,
          nickname: this.formData.nickname,
          password:
            this.formData.password === ''
              ? ''
              : this.$cms.encrypt(this.formData.password),
          phone: this.formData.phone
        },
        memberExt: {
          resourceDataId: this.formData.resourceDataId ? this.formData.resourceDataId : null,
          gender: this.formData.memberExt.gender,
          birthday: this.formData.memberExt.birthday
        }
      }
      this.updateForm(memberUpdate, params, '/shopMember/list', 'list')
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>
<style lang="scss" scoped>
.el-form-item .detail {
  color: #999999;
  font-size: 13px;
}
.unbind {
  color: #409eff;
  margin-left: 20px;
}
</style>




